<template>
  <div class="container">
    <div v-if="!loading && data && data.length">
      <datepicker v-model="picked" :upper-limit="upperLimit"/>
      <VisualizeData :data="data" :startIndex=bestIndex :endIndex="Math.ceil(bestIndex + hours - 1)"/>
    </div>
    <div>
      <input type="number" step="0.5" min="0" max="20" v-model="hours"/>
    </div>
  </div>
</template>

<script>
import VisualizeData from './components/VisualizeData.vue'
import {onMounted, ref, computed, watch} from "vue";
import Datepicker from 'vue3-datepicker'

export default {
  name: 'App',
  components: {
    VisualizeData,
    Datepicker
  },
  setup() {
    const data = ref(null);
    const loading = ref(true)
    const hours = ref(0);
    const today = new Date()
    const picked = ref(today)
    const tomorrow = new Date(today);
    tomorrow.setDate(tomorrow.getDate() + 1);
    const upperLimit = ref(tomorrow)

    const bestIndex = computed(() => {
      if (!data.value || !hours.value) return -1;
      const result = [];
      let acum = 0;
      for (let i = 0; i < data.value.length - Math.ceil(hours.value); i++) {
        for (let j = i; j < i + Math.ceil(hours.value); j++) {
          acum = acum + Number(data.value[j].PCB);
        }
        result.push(acum.toFixed(5));
        acum = 0;
      }
      const min = Math.min(...result).toFixed(5)
      return result.findIndex((num) => num === min)
    });


    const fetchData = (pickedDate) => {
      console.log(pickedDate)
      loading.value = true
      return fetch(`https://api.esios.ree.es/archives/70/download_json?locale=en&date=${pickedDate}`, {
        method: 'get',
        headers: {
          'content-type': 'application/json',
          // 'Access-Control-Allow-Origin': '*',
          // 'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE, OPTIONS'
        },
      })
          .then(res => res.json())
          .then(json => json.PVPC)
          .then(json => {
            data.value = json.map(obj => {
              obj.PCB = (obj.PCB.replace(',', '.') / 1000).toFixed(5);
              return obj;
            });
            return json;
          })
          .finally(() => loading.value = false);
    }
    watch(() => picked.value, (first) => {
      fetchData(first.toLocaleDateString().split('T')[0]);
    })
    onMounted(() => fetchData(new Date().toLocaleDateString().split('T')[0]))
    return {
      data,
      loading,
      hours,
      bestIndex,
      picked,
      upperLimit
    }
  }
}
</script>

<style>
body {
  font-size: 1.5rem;
}

.container {
  display: flex;
  gap: 5%;
}
</style>
